.testimonials {
    height: 700px;
}

.testimonials-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.testimonials-slider {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: space-evenly;
}

.testimonials-slider .slider__arrow {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.testimonials-slider__hide_wrapper {
    overflow: hidden;
}
.testimonials-slider__right img{
    transform: rotate(180deg);
}
.testimonials-slider__hide {
    display: flex;
    height: 100%;
}

.testimonials-slider__content {
    display: flex;
    width: 400%;
    height: 100%;
}

.testimonials-slider__item {
    height: 100%;
    padding: 10px;
}

.ts-el-content {
    height: 100%;
    padding: 15%;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    background: var(--gray-text-color);
    box-shadow: 0 0 10px 7px var(--gray-color);
    color: var(--light-color);
    align-items: center;
}

.ts-el-content h5 {
    text-transform: uppercase;
    letter-spacing: 2px;
    border-bottom: 2px solid;
    border-color: gray;
    padding-bottom: 5px;
    transition: all ease 500ms;
}

.ts-el-content:hover h5 {
    border-color: rgb(177, 12, 12);
}

.ts-el-content p {
    text-align: center;
}
@media (max-width: 640px) {
    .testimonials-slider{
        transform: scale(0.7);
    }
}